import request from '../utils/request'

export default {
    // Get Group list
    getGroupList(params) {
        return request({
            url: '/group/getPageGroupList',
            method: 'get',
            params: params
        })
    },
    // Add group
    addGroup(data) {
        return request({
            url: '/group/addGroup',
            method: 'post',
            data: data
        })
    },
    joinGroup(groupId) {
        return request({
            url: '/group/joinGroup',
            method: 'post',
            params: {
                groupId: groupId
            }
        })
    }
}