<template>
    <div class="groups-wrapper">
        <div class="top-wrapper">
            <div class="search-wrapper">
                <el-input placeholder="search by group id" v-model="keyword" @input="searchByKeyWord" prefix-icon="el-icon-search"></el-input>
            </div>
            <div class="create-group-wrapper" @click="showCreateModal">
                <div class="create-button">
                    <i class="el-icon-circle-plus"></i>
                </div>
                <div class="create-text">create a group</div>
            </div>
        </div>

        <div class="table-wrapper">
              <el-table
                :data="groupList"
                empty-text="No Data"
                stripe
                style="width: 100%">
                <el-table-column
                    label=""
                    width="100"
                    align="center">
                    <template slot-scope="scope">
                        <i class="el-icon-user-solid show-member-icon" :id="scope.row.id"></i>
                    </template>
                </el-table-column>
                <el-table-column
                    prop="id"
                    label="ID"
                    width="50"
                    align="center">
                </el-table-column>
                <el-table-column
                    prop="studentCount"
                    label="Number of team members"
                    width="250"
                    align="center">
                </el-table-column>
                <el-table-column
                    prop="preferField"
                    label="Group prefer field">
                </el-table-column>
                <el-table-column
                    label=""
                    width="150">
                    <template slot-scope="scope">
                        <div class="join-button" :id="scope.row.id" @click="joinGroup(scope.row.id)">JOIN</div>
                    </template>
                </el-table-column>
            </el-table>
            <div class="pagination-wrapper" v-show="groupTotal > limit">
                <el-pagination
                    background
                    layout="prev, pager, next"
                    @current-change="handleCurrentChange"
                    :current-page="currentPage"
                    :page-size="limit"
                    :total="groupTotal">
                </el-pagination>
            </div>
        </div>
        <modal 
            v-on:hideModal="hideModal"
            title="Add Group"
            :isShow="isShowAddModal"
            class="add-group-modal">
            <template v-slot:content>
                <div class="prefer-field-wrapper">
                    <div class="label">group prefer field:</div>
                    <el-input placeholder="prefer field" v-model="preferField"></el-input>
                </div>
            </template>

            <template v-slot:footer>
                <div class="confirm-btn-wrapper">
                    <div class="confirm-btn" @click="addGroup">OK</div>
                </div>
            </template>
        </modal>
    </div>
</template>

<script>
import Modal from '../../components/Modal'
import groupsApi from '@/api/groups'
import CONSTANTS from '@/utils/constants'
export default {
    data() {
        return {
            groupList:[],
            isShowAddModal: false,
            preferField: '',
            groupTotal: 0,
            limit: 8,
            currentPage: 1,
            keyword: ''
        }
    },
    components: {
        Modal
    },
    created() {
        this.getGroupList()
    },
    methods: {
        showCreateModal() {
            this.isShowAddModal = true
            this.preferField = ''
        },
        hideModal() {
            this.isShowAddModal = false
            this.preferField = ''
        },
        searchByKeyWord() {
            this.currentPage = 1
            this.getGroupList()
        },
        handleCurrentChange(val) {
            this.currentPage = val
            this.getGroupList()

        },
        joinGroup(grouId) {
            this.$confirm("Are you sure join this group?", 'Confirm', {
                confirmButtonText: 'Confirm',
                cancelButtonText: 'Cancel'
            }).then(() => {
                groupsApi.joinGroup(grouId).then(response => {
                    if (response.code == CONSTANTS.RESPONSE_CODE.SUCCESS) {
                        this.$notify({
                            title: 'Success',
                            message: 'Join group successfully',
                            type: 'success'
                        })

                        this.getGroupList()
                    }
                })
            })
        },
        addGroup () {
            groupsApi.addGroup({
                preferField: this.preferField
            }).then(response => {
                if (response.code == CONSTANTS.RESPONSE_CODE.SUCCESS) {
                    this.$notify({
                        title: 'Success',
                        message: 'Your group ID is ' + '"' + response.data.group.id + '"',
                        type: 'success'
                    });
                    this.hideModal()
                    this.getGroupList()
                } else {
                    this.$notify.error({
                        title: 'Error',
                        message: 'Failed add the group'
                    });
                }
            })
        },
        getGroupList() {
            groupsApi.getGroupList({
                keyword: this.keyword,
                page: this.currentPage,
                limit: this.limit
            }).then(response => {
                this.groupTotal = response.data.total
                let items = []
                for (let item of response.data.list) {
                    items.push({
                        id: item.id,
                        studentCount: item.studentList.length,
                        preferField: item.preferField
                    })
                }
                this.groupList = items
            })
        }
    }
}
</script>

<style lang="scss" scoped>
.groups-wrapper {
    display: flex;
    flex-direction: column;
    height: 100%;
    padding-left: 100px;
    .create-group-wrapper {
        display: flex;
        justify-content: center;
        align-items: center;
        cursor: pointer;

        .create-button {
            margin-right: 10px;
            .el-icon-circle-plus {
                color: #f5df4e;
                font-size: 25px;
            }
        }
    }

    .add-group-modal {
        .prefer-field-wrapper {
            display: flex;
            align-items: center;
            .label {
                margin-right: 10px;
            }
        }
    }

    .top-wrapper {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 20px;
    }
    .search-wrapper {
        width: 300px;
    }
    .table-wrapper {
        flex: 1;
        background-color: white;
        padding: 30px;
        position: relative;
        .show-member-icon {
            font-size: 18px;
        }
        .join-button {
            border: 1px solid #f5df4e;
            border-radius: 5px;
            color: #f5df4e;
            text-align: center;
            cursor: pointer;
            padding: 5px 25px;
            display: inline-block;
        }
    }
    .prefer-field-wrapper {
        .label {
            white-space: nowrap;
            margin-right: 30px;
            font-size: 14px;
        }
    }
    .pagination-wrapper {
        position: absolute;
        bottom: 30px;
        right: 30px;
    }
    .confirm-btn-wrapper {
        text-align: right;
        .confirm-btn {
            padding: 10px 30px;
            border-radius: 5px;
            color: white;
            font-size: 18px;
            background-color: #f5df4e;
            cursor: pointer;
            display: inline-block;
        }
    }
    ::v-deep .el-pagination.is-background .el-pager li:not(.disabled).active {
        background-color: #f5df4e;
    }
    ::v-deep .el-pagination.is-background .el-pager li:not(.disabled):hover {
        color: #f5df4e;
    }

}
</style>